import androidx.compose.runtime.CompositionLocalProvider
import com.varabyte.kobweb.core.AppGlobalsLocal
import com.varabyte.kobweb.navigation.RoutePrefix
import com.varabyte.kobweb.navigation.Router
import com.varabyte.kobweb.navigation.UpdateHistoryMode
import com.varabyte.kobweb.silk.components.animation.registerKeyframes
import com.varabyte.kobweb.silk.defer.renderWithDeferred
import kotlin.Unit
import kotlinx.browser.document
import kotlinx.browser.window
import org.jetbrains.compose.web.renderComposable

public fun main(): Unit {
    RoutePrefix.set("")
    val router = Router()
    com.varabyte.kobweb.core.init.initKobweb(router) { ctx ->
        ctx.router.register("/") { carlosgub.dev.pages.HomePage() }

    }
    router.addRouteInterceptor {
        path = path.removeSuffix(".html").removeSuffix(".htm")
    }

    com.varabyte.kobweb.silk.init.initSilkHook = { ctx ->
        ctx.theme.registerComponentStyle(carlosgub.dev.components.styles.IntroSectionStyle)
        ctx.theme.registerComponentStyle(carlosgub.dev.components.styles.FormCheckInput)
        ctx.theme.registerComponentStyle(carlosgub.dev.components.styles.FormCheck)
        ctx.theme.registerComponentStyle(carlosgub.dev.components.styles.BackgroundSectionStyle)
        ctx.theme.registerComponentStyle(carlosgub.dev.components.styles.BackToTopButtonStyle)
        ctx.theme.registerComponentStyle(carlosgub.dev.components.styles.components.UlStyle)
        ctx.theme.registerComponentStyle(carlosgub.dev.components.styles.components.LinkStyle)
        ctx.theme.registerComponentStyle(carlosgub.dev.components.styles.components.H1Style)
        ctx.theme.registerComponentStyle(carlosgub.dev.components.styles.components.H2Style)
        ctx.theme.registerComponentStyle(carlosgub.dev.components.styles.components.H3Style)
        ctx.theme.registerComponentStyle(carlosgub.dev.components.styles.components.H6Style)
        ctx.theme.registerComponentStyle(carlosgub.dev.components.styles.components.PStyle)
        ctx.theme.registerComponentStyle(carlosgub.dev.components.styles.FooterStyle)
        ctx.theme.registerComponentStyle(carlosgub.dev.components.styles.LinkFooterStyle)
        ctx.theme.registerComponentStyle(carlosgub.dev.components.styles.ArrowUpStyle)
        ctx.theme.registerComponentStyle(carlosgub.dev.components.styles.DevelopedByLink)
        ctx.theme.registerComponentStyle(carlosgub.dev.components.styles.SectionModifier)
        ctx.theme.registerComponentStyle(carlosgub.dev.components.styles.ExperienceSectionStyle)
        ctx.theme.registerComponentStyle(carlosgub.dev.components.styles.ReadMyResumeStyle)
        ctx.theme.registerComponentStyle(carlosgub.dev.components.styles.CompanyLink)
        ctx.theme.registerComponentStyle(carlosgub.dev.components.styles.PExperienceStyle)
        ctx.stylesheet.registerKeyframes(carlosgub.dev.components.styles.IntroContainerKeyFrames)
        ctx.stylesheet.registerKeyframes(carlosgub.dev.components.keyframe.FadeInKeyFrames)
        ctx.stylesheet.registerKeyframes(carlosgub.dev.components.keyframe.Shake)
        ctx.stylesheet.registerKeyframes(carlosgub.dev.components.keyframe.WidthKeyFrames)
        carlosgub.dev.initSilk(ctx)
    }

    router.navigateTo(window.location.href.removePrefix(window.location.origin),
            UpdateHistoryMode.REPLACE)

    // For SEO, we may bake the contents of a page in at build time. However, we will overwrite them
    // the first time we render this page with their composable, dynamic versions. Think of this as
    // poor man's hydration :)
    // See also: https://en.wikipedia.org/wiki/Hydration_(web_development)
    val root = document.getElementById("root")!!
    while (root.firstChild != null) {
        root.removeChild(root.firstChild!!)
    }

    renderComposable(rootElementId = "root") {
        CompositionLocalProvider(
            AppGlobalsLocal provides mapOf("title" to "Carlos Ugaz")
        ) { carlosgub.dev.MyApp {
              router.renderActivePage { renderWithDeferred { it() } }
        } }
    }
}
